import { Component } from '@angular/core';
import { NavController, Events } from 'ionic-angular';
import { DetailPage } from '../detail/detail';
import { Storage } from '@ionic/storage';
import configs from '../../app/config';
import { Device } from '../../models/device';

@Component({
  selector: 'page-home',
  templateUrl: 'home.html'
})
export class HomePage {
  items=[];

  state={
    data:{}
  }

  sensors={
    data:{}
  }

  device: Device = {
    id: null,
    params: {
      name: null,
      host: null,
      port: 0,
      username: null,
      password: null,
      default: false
    },
    data: null
  };

  constructor(public navCtrl: NavController, private storage: Storage, events: Events) {
    // state data
    events.subscribe('state:feature', data => this.state = data);
    events.subscribe('state:sensors', result => {
      for(const k in result.data) {
        if(typeof result.data[k] == 'number' && result.data[k] == -500) {
          result.data[k] = '-';
        }
      }
      this.sensors = result;
    });
  }

  ionViewWillEnter() {
    this.storage.get('settings').then(r => {
      this.items = r.devices;
    });
    this.device = configs.currentDevice;
  }

  ionViewDidLoad() {
  }

  goToDetail(target:string) {
    this.navCtrl.push(DetailPage);
  }

  goToPage(p) {
    this.navCtrl.push(p);
  }
}



