import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, LoadingController, Loading, AlertController, Events } from 'ionic-angular';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AuthServiceProvider } from '../../providers/auth-service/auth-service';
import { Storage } from '@ionic/storage';
import { TabsPage } from '../tabs/tabs';
import { ListGrowPage } from '../list-grow/list-grow';
import { IntroPage } from '../intro/intro';
import config from '../../app/config';

@IonicPage()
@Component({
  selector: 'page-add-grow',
  templateUrl: 'add-grow.html',
})
export class AddGrowPage {

  form: FormGroup;
  loading: Loading;
  id: string;
  data = {
    name: null,
    host: null,
    port: 9090,
    username: null,
    password: null,
    default: false
  };

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private formBuilder: FormBuilder,
    private loadingCtrl: LoadingController,
    private alertCtrl: AlertController,
    private auth: AuthServiceProvider,
    private storage: Storage,
    public events: Events
  ) {
      this.form = this.formBuilder.group({
        name: [this.data.name, [
          Validators.required,
          Validators.maxLength(13),
          Validators.minLength(5)
        ]],
        host: [this.data.host, [
          Validators.required
        ]],
        port: [this.data.port, [
          Validators.required
        ]],
        username: [this.data.username, [
          Validators.required
        ]],
        password: [this.data.password, [
          Validators.required
        ]],
        default: [this.data.default]
      });

      this.id = navParams.get('id');
  }

  ionViewDidLoad() {
    if(this.id) {
      this.storage.get('settings').then(r => {
        this.data =(r.devices.filter(item => item.id == this.id)[0]||{}).params;
      });
    }
  }

  showError(error) {
    this.loading.dismiss();

    let message = error.statusText;
    if(error.status == 401) {
      message = 'Invalid username or password'
    }

    let response;
    try {
      response = JSON.parse(error._body);
    }catch(e) {}

    const alert = this.alertCtrl.create({
      title: error.status == 401 ? error.statusText : (error.title ? error.title : 'Fail'),
      subTitle: response ? response.message : message,
      buttons: ['OK']
    });
    alert.present();
  }

  onSubmit() {
    this.showLoading();

    const login = this.auth.authenticate({
      username: this.form.value.username,
      password: this.form.value.password
    }, {
      host: this.form.value.host,
      port: this.form.value.port
    });

    login.subscribe(r => {

      this.storage.get('settings').then(settings => {
        if(this.form.value.default) {
          settings.devices = settings.devices.map(item => {
            item.params.default = false;
            return item;
          });
        }

        // remove invalids
        settings.devices = settings.devices.filter(item => item.id);

        // generate unique id
        const uniqid = String.fromCharCode(65 + Math.floor(Math.random() * 26)) + Date.now();
        let data = {
          id: uniqid,
          params: this.form.value,
          data: r.data
        }

        // Edit current device
        if(this.id) {
          settings.devices = settings.devices.map(item => {
            if(item.id == this.id) {
              data = Object.assign(data, {
                id: this.id
              });
              item = data;
            }
            return item;
          });
        // add the new device
        }else {
          settings.devices.push(data);
        }

        // Set as current device
        config.currentDevice = data;

        // store settigns and go to tabs
        this.storage.set('settings', settings).then(r => {
          this.events.publish('devices:update', data);
          this.navCtrl.push(TabsPage);
        }).catch(e => this.showError({
          statusText: e.message,
          title: 'Login error'
        }));
      });
    }, error => this.showError(error));
  }

  remove(id:string) {
    const alert = this.alertCtrl.create({
      title: 'Remove device',
      subTitle: 'Are you sure you want to remove this device?',
      buttons: [{
        text: 'Cancel',
        role: 'cancel'
      },
      {
        text: 'Remove',
        handler: data => {
          this.showLoading();
          this.storage.get('settings').then(settings => {
            if(!settings) {
              return this.navCtrl.push(ListGrowPage);
            }
            settings.devices = settings.devices.filter(item => item.id != id);
            this.storage.set('settings', settings).then(r => {
              this.events.publish('devices:update');
              if(config.currentDevice.id == id) {
                // Mark another device as the current device, if the deleted one is the current one.
                config.currentDevice = settings.devices.length > 0 ? settings.devices[0] : null;
              }
              if(!config.currentDevice) {
                return this.navCtrl.push(IntroPage);
              }
              this.navCtrl.popTo(ListGrowPage);
            }).catch(e => {
              console.log(e)
              this.showError({
                statusText: e.message,
                title: 'Fail'
              })
            });
          });
        }
      }]
    });
    alert.present();
  }

  showLoading() {
    this.loading = this.loadingCtrl.create({
      content: 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }
}



