import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { Storage } from '@ionic/storage';
import { AddGrowPage } from '../add-grow/add-grow';
import { TabsPage } from '../tabs/tabs';
import config from '../../app/config';

@IonicPage()
@Component({
  selector: 'page-list-grow',
  templateUrl: 'list-grow.html',
})
export class ListGrowPage {

  items=[];
  current: boolean = false;
  disableBackButton: boolean = false;

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private storage: Storage) {
      if(navParams.get('disableBackButton') !== undefined) {
        this.disableBackButton = navParams.get('disableBackButton');
      }
  }

  ionViewWillEnter() {
    this.current = !!config.currentDevice.id;
    this.storage.get('settings').then(r => {
      this.items = r.devices;
    });
  }

  goToPage(id:string){
    this.navCtrl.push(AddGrowPage,{id});
  }

  goToHome(){
    this.navCtrl.push(TabsPage);
  }
}



