import { Injectable } from '@angular/core';
import { Http, Headers, RequestOptions } from '@angular/http';
import { Events } from 'ionic-angular';

import 'rxjs/add/operator/map';
import 'rxjs/add/operator/do';
import 'rxjs/add/operator/catch';

import config from '../../app/config';

@Injectable()
export class StateServiceProvider {

  options: RequestOptions;
  apiHost: string;
  pollings = {};
  services = [{
    service: 'feature',
    channel: 'feature',
    interval: 3000,
    default: {
      light: {
          on: false
      },
      fan: {
          on: false
      },
      'o2-pump': {
          on: false
      },
      'co2-pump': {
          on: false
      }
    }
  },{
    service: 'sensors',
    channel: 'sensors',
    interval: 3000,
    default: {
      'air-temperature': 0,
      'air-humidity': 0,
      'light': 0,
      'light-state': '-'
    }
  }];

  constructor(
    private http: Http,
    public events: Events
  ) {}

  start() {
    for(const item of this.services) {
      this.polling(item.service, {
        channel: item.channel,
        interval: item.interval,
        default: item.default
      })
    }
  }

  private polling(service:any, options:any = {channel: 'default', interval: 5000, default: null}) {
    this.polling[options.channel] = setInterval(() => {
      const channel = 'state:' + options.channel;

      if(!config.currentDevice.params.host || !config.currentDevice.params.port) {
        return this.events.publish(channel, {
          error: true,
          data: options.default
        });
      }

      const params = config.currentDevice.params;
      this.apiHost = `http://${params.host}:${params.port}`;

      this[service]().subscribe(result => {
        this.events.publish(channel, {
          error: false,
          data: result
        });
      }, error => {
        this.events.publish(channel, {
          error: true,
          data: options.default
        });
      })
    }, options.interval);
  }

  getDefaultHeaders() {
    const headers = new Headers();
    headers.append('Accept', 'application/json');
    headers.append('Content-Type', 'application/json');
    return headers;
  }

  feature(data, connection = null){
    this.apiHost = connection ? `http://${connection.host}:${connection.port}` : this.apiHost;
    const options = new RequestOptions({headers: this.getDefaultHeaders()});
    return this.http.get(`${this.apiHost}/state/feature`, options)
      .map(res => res.json());
  }

  sensors(data, connection = null){
    this.apiHost = connection ? `http://${connection.host}:${connection.port}` : this.apiHost;
    const options = new RequestOptions({headers: this.getDefaultHeaders()});
    return this.http.get(`${this.apiHost}/state/sensors`, options)
      .map(res => res.json());
  }

}



