import { Component, ViewChild } from '@angular/core';
import { Platform, NavController, ModalController, MenuController, AlertController, LoadingController, Loading, Events } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { TabsPage } from '../pages/tabs/tabs';
import { HomePage } from '../pages/home/home';
import { ListGrowPage } from '../pages/list-grow/list-grow';
import { IntroPage } from '../pages/intro/intro';

import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { StateServiceProvider } from '../providers/state-service/state-service';
import { Storage } from '@ionic/storage';

import configs from '../app/config';
import { AddGrowPage } from '../pages/add-grow/add-grow';
import config from '../app/config';

//import { Http } from '@angular/http';

//import { SplashPage } from '../pages/splash/splash';

@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild('mycontent') nav: NavController
  rootPage:any
  homePage:any;
  loading: Loading;

  settings = {
    devices:[]
  }

  current: string;

  menuItems=[];

  constructor(platform: Platform,
    private alertCtrl: AlertController,
    statusBar: StatusBar,
    splashScreen: SplashScreen,
    modalCtrl: ModalController,
    private storage: Storage,
    private auth: AuthServiceProvider,
    private stateService: StateServiceProvider,
    private loadingCtrl: LoadingController,
    public events: Events,
    public menuCtrl: MenuController
  ) {
    this.homePage = HomePage;

    platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      statusBar.styleDefault();
      splashScreen.hide();

      const perform = () => {
        this.storage.get('settings').then(settings => {
          if(!settings || ((settings||{}).devices||[]).length == 0) {
            this.rootPage = IntroPage;
            return;
          }

          this.stateService.start();
          this.settings = settings;

          const stack = [];
          const containsDefault = settings.devices.filter(item => item.params.default === true)[0];
          if(containsDefault) {
            stack.push(() => new Promise((resolve, reject) => {
              this.auth
                .check(containsDefault.data.session, containsDefault.params)
                .subscribe(success => resolve(containsDefault), error => reject(error));
            }));
            this.showLoading();
            this.defineConnection(stack).then(r => {
              this.loading.dismiss();
              this.rootPage = TabsPage;
            }).catch(err => {
              this.loading.dismiss();
              const alert = this.alertCtrl.create({
                title:  'Authorization failed',
                subTitle: 'Unable to connect to default device. Please check the parameters (host, port, usernam and password)',
                buttons: ['OK']
              });
              alert.present();
              this.rootPage = ListGrowPage;
              setTimeout(() => {
                this.nav.push(AddGrowPage, {id:containsDefault.id});
              },1);
            });
            return;
          }else {
            for(const item of settings.devices) {
              stack.push(() => new Promise((resolve, reject) => {
                this.auth
                  .check(item.data.session, item.params)
                  .subscribe(success => resolve(item), error => reject(error));
              }));
            }
          }

          this.showLoading();
          this.defineConnection(stack).then(r => {
            this.loading.dismiss();
            this.rootPage = TabsPage;
          }).catch(err => {
            this.loading.dismiss();
            const alert = this.alertCtrl.create({
              title:  'Authorization failed',
              subTitle: 'Could not connect to any of the configured devices. Please check the parameters (host, port, usernam and password)',
              buttons: ['OK']
            });
            alert.present();
            this.rootPage = ListGrowPage;
          });
        }).catch(err => {
          this.rootPage = IntroPage;
        });
      }

      this.storage.get('settings').then(settings => {
        if(!settings) {
          this.storage.set('settings', this.settings).then(() => perform());
          return;
        }
        perform();
      });

      // const splash = modalCtrl.create(SplashPage);
      // splash.present();
    });

    // Update the menu
    events.subscribe('devices:update', data => {
      if(data) {
        this.current = data.id;
      }
      this.storage.get('settings').then(settings => {
        if(!settings) {
          this.settings.devices = [];
          return;
        }
        this.settings.devices = settings.devices;
      });
    });

    // this.http.connection.subscribe((isOnline) => {
    //   console.log(isOnline,'------------------>');
    // });
  }

  goToDevices() {
    this.nav.setRoot(ListGrowPage);
  }

  setDevice(id:string) {
    // Set the current device
    const selectedDevice = this.settings.devices.filter(item => item.id == id)[0];

    this.showLoading('Connecting to the device. Please wait....');
    this.auth
      .check(selectedDevice.data.session, selectedDevice.params)
      .subscribe(success => {
          this.menuCtrl.close();
          this.loading.dismiss();

          configs.currentDevice = selectedDevice;
          this.current = configs.currentDevice.id;

          this.nav.push(TabsPage);
        }, error => {
          this.loading.dismiss();
          const alert = this.alertCtrl.create({
            title:  'Authorization failed',
            subTitle: `Unable to connect to device <b>${selectedDevice.params.name}</b>. Please check the parameters (host, port, usernam and password)`,
            buttons: [{
              text:'OK',
              handler: () => {
                if(config.currentDevice.id == id) {
                  this.menuCtrl.close();
                  this.nav.setPages([
                    {page: ListGrowPage, params: {disableBackButton: true}},
                    {page: AddGrowPage, params: {id: selectedDevice.id}}
                  ]);
                }
              }
            }]
          });
          alert.present();
        });
  }

  defineConnection(promises) {
    if(!Array.isArray(promises)) {
      return Promise.reject(new Error('defineConnection expects to receive an array'));
    }else {
      for(const p of promises) {
        if(p instanceof Promise) {
          return Promise.reject(new Error('To use the Promises queue, all items must be a function that returns a Promise and not an instance of Promise'));
        }
      }
    }

    return new Promise((resolve, reject) => {
      const self = this;
      (function fire(){
        if(!promises.length || promises.length == 0) {
          return reject();
        }
        promises.shift()().then(r => {
          configs.currentDevice = r;
          self.current = r.id;
          resolve(r);
        },
        (e) => {
          fire();
        });
      }());
    });
  }

  showLoading(message:string = '') {
    this.loading = this.loadingCtrl.create({
      content: message || 'Please wait...',
      dismissOnPageChange: true
    });
    this.loading.present();
  }
}



