import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { AddGrowPage } from '../add-grow/add-grow';

/**
 * Generated class for the IntroPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-intro',
  templateUrl: 'intro.html',
})
export class IntroPage {
  slides = [
    {
      title: "Highgarden",
      description: "The <b>Highgarden</b> ipsum dolor sit amet, consectetur adipiscing elit. Phasellus eu dictum metus.",
      image: "assets/imgs/plant.svg",
    },
    {
      title: "Plug & Plant!",
      description: "The <b>Highgarden</b> ipsum dolor sit amet, consectetur adipiscing elit. Phasellus eu dictum metus.",
      image: "assets/imgs/plant.svg",
    }
  ];
  constructor(public navCtrl: NavController, public navParams: NavParams) {
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad IntroPage');
  }

  addGrowPage(){
    this.navCtrl.push(AddGrowPage);
  }

}



