import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { HttpModule, Http, XHRBackend, RequestOptions } from '@angular/http';
import { IonicStorageModule } from '@ionic/storage';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { MyApp } from './app.component';

import { MenuPage } from '../pages/menu/menu';
import { AboutPage } from '../pages/about/about';
import { ContactPage } from '../pages/contact/contact';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';
import { SplashPage } from '../pages/splash/splash';
import { DetailPage } from '../pages/detail/detail';
import { AddGrowPage } from '../pages/add-grow/add-grow';
import { ListGrowPage } from '../pages/list-grow/list-grow';
import { IntroPage } from '../pages/intro/intro';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { AuthServiceProvider } from '../providers/auth-service/auth-service';
import { SettingsServiceProvider } from '../providers/settings-service/settings-service';

// Http Interceptor
import { httpFactory } from "../providers/http/http.factory";
import { PubSubService } from '../providers/http/pubsub-service';
import { StateServiceProvider } from '../providers/state-service/state-service';


@NgModule({
  declarations: [
    MyApp,
    SplashPage,
    MenuPage,
    TabsPage,
    HomePage,
    DetailPage,
    AboutPage,
    ContactPage,
    AddGrowPage,
    ListGrowPage,
    IntroPage
  ],
  imports: [
    BrowserModule,
    HttpModule,
    IonicModule.forRoot(MyApp),
    IonicStorageModule.forRoot()
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    SplashPage,
    TabsPage,
    MyApp,
    AboutPage,
    ContactPage,
    HomePage,
    MenuPage,
    DetailPage,
    AddGrowPage,
    ListGrowPage,
    IntroPage
  ],
  providers: [
    StatusBar,
    SplashScreen,
    AuthServiceProvider,
    {provide: ErrorHandler, useClass: IonicErrorHandler},
    SettingsServiceProvider,
    PubSubService,
    {
      provide: Http,
      useFactory: httpFactory,
      deps: [XHRBackend, RequestOptions, PubSubService]
    },
    StateServiceProvider
  ]
})
export class AppModule {}



